body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.app-wrapper{min-height:101vh;display:grid;grid-template-areas:"header" "mainContent" "footer";grid-template-rows:80px 1fr 150px}.app-wrapper__mainContent{grid-area:mainContent}

.Preloader_preloaderLarge__2rNhS{
    margin-bottom: 20px;
    margin-top: 20px;
    height: 50vh;
    width: 50vw;
    position: absolute;
    left: 25vw;
    bottom: 25vh;
}
.header{grid-area:header;display:flex;flex-direction:row;justify-content:space-between;width:100%;height:80px;background-color:#44C716}.header__left{order:1;justify-self:start;display:flex;flex-direction:row;margin-left:2vw}.header__left__appLogo img{margin-top:6px;height:68px;width:68px}.header__left__appName{align-self:center;margin-left:20px;font-weight:500;font-size:24px;line-height:28px;letter-spacing:0.128571px;color:#000;white-space:nowrap;text-decoration:none}.header__right{order:3;justify-self:end;display:flex;flex-direction:row}.header__right__links{display:flex;flex-direction:row;margin-right:2vw}.header__right__links a{align-self:center;margin-right:15px;margin-left:15px;font-weight:500;font-size:24px;line-height:28px;letter-spacing:0.128571px;color:#000;text-decoration:none}.header__right__links a:hover{color:darkcyan}.header__right__icons{align-self:center;margin-right:8vw;display:flex;flex-direction:row}.header__right__icons img{height:50px;width:50px;margin-right:20px}

.footer{grid-area:footer;display:flex;flex-direction:row;justify-content:space-between;width:100%;height:150px;background-color:#44C716}.footer__left{display:flex;flex-direction:column;opacity:100%}.footer__left__top{display:flex;flex-direction:row;margin-left:20px;margin-top:20px;font-weight:500;font-size:18px;line-height:26px;letter-spacing:0.128571px}.footer__left__top__column{display:flex;flex-direction:column;padding-right:10px;margin-right:2vw}.footer__left__top__column a{margin-top:5px;text-decoration:none;white-space:nowrap;color:black}.footer__left__bottom{margin-top:15px;display:flex;flex-direction:row;align-items:center;padding-right:3vw;padding-top:8px;border-top:black 1px solid;font-weight:500;font-size:16px;line-height:22px;letter-spacing:0.128571px;opacity:80%;white-space:nowrap}.footer__left__bottom__company{margin-left:20px;margin-right:3vw}.footer__left__bottom__number{display:flex;flex-direction:row;align-items:center}.footer__left__bottom__number img{width:25px;height:25px;margin-right:6px}.footer__right{align-self:center;display:flex;flex-direction:row;margin-right:4vw}.footer__right a img{width:40px;height:40px;margin-right:10px;margin-left:10px}


